import { Card } from '@/types/statistics';
import UserRemoveIcon from '@/components/icons/user/UserRemove';
import TwoUsersIcon from '@/components/icons/user/TwoUsersIcon';
import MinusUserIcon from '@/components/icons/user/MinusUserIcon';
import GroupIcon from '@/components/icons/user/GroupIcon';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { employeesStats } from '@/features/humanResources/types/employeesStats.types';
import ChartsComponent from '../components/invoicingList/charts/ChartsComponent';
import ReturnsListStatistics from '../components/returns/ReturnsListStatistics';
import ReturnsList from '../components/returns/ReturnsList';

export default function CreditorsPage() {
    const [stats, setStats] = useState<employeesStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.employee.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'إجمالي المرتجعات', num: stats?.total_employees, percent: 12, icon: <GroupIcon /> },
        { text: 'إجمالي قيمة المرتجعات', num: 35, percent: 0, icon: <UserRemoveIcon /> },
        { text: 'نسبة المرتجعات', num: stats?.total_full_time, percent: -0.3, icon: <TwoUsersIcon /> },
        { text: 'المرتجعات المرفوضة', num: stats?.total_part_time, percent: -0.3, icon: <MinusUserIcon /> },
    ];

    return (
        <div className="w-full mt-6 mb-20">
            <ReturnsListStatistics cards={cards} />
            <ChartsComponent />
            <ReturnsList />
        </div>
    );
}

